@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
	--primary-white: #fff;
	--primary-light-grey: #f4f4f4;
	--primary-grey: #ccd1d9;
	--primary-dark-grey: #666666;
	--primary-orange: #f9ab00;
	--primary-dark: rgb(52, 58, 64);
	--primary-red: #ef4035;
	--primary-hover-red: #f33501;
	--primary-black: #000000;
}

body {
	font-family: "Roboto Condensed", sans-serif !important;
}
/*****************NAVBAR*********************/
nav {
	z-index: 2;
}
.logo {
	width: 10.25rem;
}
/************MENU LINKS*********************/

nav a.nav-link {
	font-size: 0.8rem;
	font-weight: 350;
	letter-spacing: 0.0375rem;
	cursor: pointer;
}

nav ul li {
	text-transform: uppercase;
}

.navbar-nav > li > a {
	margin-right: 0.75rem;
	margin-left: 0.75rem;
	color: #fff !important;
	color: var(--primary-white) !important;
	border-top: 0.1875rem solid rgb(52, 58, 64);
	border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
	width: max 17.25em;
	padding: 0 !important;
	height: 6rem !important;
}
@media (max-width: 768px) {
	.navbar-brand {
		width: 7rem;
	}
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
	border-top: 0.1875rem solid #f9ab00;
	border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
	border-top: 0.1875rem solid #f9ab00;
	border-top: 0.1875rem solid var(--primary-orange);
}

/***************** HEADER *********************/

.header-wrapper {
	position: relative;
	background: url(/Portfolio/static/media/portfolio-background.50eeee6b.jpeg) no-repeat;
	background-size: cover;
	background-position: center;
	height: 90vh;
}

.main-info {
	display: flex;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;

	z-index: 1;
}

.main-info h1 {
	color: #f9ab00;
	color: var(--primary-orange);
	text-transform: uppercase;
}

/* TYPED TEXT */
.typed-text {
	font-size: 2rem;
	color: #fff;
	color: var(--primary-white);
}
@media (max-width: 768px) {
	.main-info h1 {
		font-size: 1.5rem !important;
	}
	.typed-text {
		font: size 1.7rem !important;
	}
}

/* ------------ HEADER MAIN OFFER BUTTON------------ */
.btn-main-offer {
	border: 1px solid #ef4035;
	border: 1px solid var(--primary-red);
	text-transform: uppercase;
	border-radius: 0;
	padding: 0.625rem 0.75rem 0.75rem 0.75rem;
	color: #fff;
	color: var(--primary-white);
	margin: 2rem;
}

.btn-main-offer:hover {
	text-decoration: none;
	background-color: #f33501;
	background-color: var(--primary-hover-red);
	color: #fff;
	color: var(--primary-white);
	transition: 0.2s ease-in-out;
}

/* ------PARTICLES JS------- */
.tsparticles-canvas-el {
	position: absolute;
	height: 100vh;
	width: 100vw;
	z-index: 1;
}

@media (max-width: 768px) {
	.particles-canvas {
		display: none;
	}
}

/*****************ABOUT ME*********************/
.photo-wrap {
	display: flex;
	justify-content: center;
	align-items: center;
}

.profile-img {
	width: 16rem;
	border-radius: 50%;
	padding: 0.3rem;
	border: 1px solid #f9ab00;
	border: 1px solid var(--primary-orange);
}

.about-heading {
	color: #f9ab00;
	color: var(--primary-orange);
	text-transform: uppercase;
}
@media (max-width: 468px) {
	.profile-img {
		width: 10rem;
	}
}

/* ------------Education----------------- */
.Education {
	text-align: center;
	padding-bottom: 2rem;
	background: #f4f4f4;
	background: var(--primary-light-grey);
}

.Education h1 {
	color: rgb(52, 58, 64);
	color: var(--primary-dark);
	text-transform: uppercase;
}

