/* ------EXPERIENCE------- */

.experience {
	padding: 0.7rem;
}

.experience-wrapper {
	position: relative;
	padding: 3.125rem 0;
	overflow: hidden;
}

.experience h1 {
	text-transform: uppercase;
	color: #f9ab00;
	color: var(--primary-orange);
}

.experience-wrapper::before {
	position: absolute;
	content: "";
	top: 0;
	left: 50%;
	margin-left: -0.0625rem;
	width: 0.125rem;
	height: 100%;
	background: #ccd1d9;
	background: var(--primary-grey);
	z-index: 1;
}

.timeline-block {
	width: calc(50% + 0.5rem);
	display: flex;
	justify-content: space-between;
	clear: both;
}

.timeline-block-right {
	float: right;
}

.timeline-block-left {
	float: left;
	direction: rtl;
}

.marker {
	width: 1rem;
	height: 1rem;
	border-radius: 50%;
	border: 0.3125rem solid rgb(52, 58, 64);
	border: 0.3125rem solid var(--primary-dark);
	background: #f9ab00;
	background: var(--primary-orange);
	margin-top: 0.625rem;
	z-index: 3;
}

.timeline-content {
	width: 95%;
	padding: 0 0.9375rem;
	color: #666666;
	color: var(--primary-dark-grey);
	direction: ltr;
}

.timeline-content h3 {
	margin-top: 0.3125rem;
	color: #ef4035;
	color: var(--primary-red);
	font-weight: 900;
}

/* -----------MEDIA---------- */

@media (max-width: 768px) {
	.experience .container {
		padding-left: 0;
	}

	.experience-wrapper::before {
		left: 0.5rem;
		margin-bottom: 1.875rem;
	}
	.timeline-block-right {
		float: none;
		width: 100%;
	}

	.timeline-block-left {
		float: none;
		direction: ltr;
		width: 100%;
	}
}

/* ------Portfolio---------- */
.portfolio-wrapper {
	background: #f4f4f4;
	background: var(--primary-light-grey);
	padding: 3rem 0;
}

.portfolio-wrapper h1 {
	color: #f9ab00;
	color: var(--primary-orange);
}

.portfolio-image {
	width: 30rem;
	border: 1px solid #f9ab00;
	border: 1px solid var(--primary-orange);
	padding: 0 0.5rem;
}

.portfolio-image-box {
	position: relative;
	margin: 1rem;
}

/* ---------Overflow--- */
.overflow {
	position: absolute;
	opacity: 0;
	background: #000000;
	background: var(--primary-black);
	width: 30rem;
	height: 16rem;
	top: 0;
	border: 1px solid #ef4035;
	border: 1px solid var(--primary-red);
	cursor: pointer;
}

.portfolio-image-box:hover .overflow {
	opacity: 60%;
	transition: 0.3s ease-in-out;
}

.portfolio-icon {
	position: absolute;
	opacity: 0;
	margin-left: -8rem;
	margin-top: 3.6rem;
	color: #f9ab00;
	color: var(--primary-orange);
	font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
	opacity: 70%;
	transition: 0.3s ease-in-out;
	cursor: pointer;
}

/* --------POPUP----------- */
.portfolio-image-popupbox {
	width: 45rem;
	padding: 0 0.5rem;
}

.hyper-link {
	cursor: pointer;
	color: #f9ab00;
	color: var(--primary-orange);
}

.hyper-link:hover {
	color: #ef4035;
	color: var(--primary-red);
}

/* media */
@media (max-width: 768px) {
	.portfolio-image-popupbox {
		width: 100%;
	}
}

@media (max-height: 570px) {
	.popupbox-wrapper {
		height: 100% !important;
	}
	.portfolio-image-popupbox {
		width: 80%;
	}
}

@media (max-height: 280px) {
	.popupbox-wrapper {
		height: 90% !important;
	}
	.portfolio-image-popupbox {
		width: 30%;
	}
}

/* --------------Contact Me Component------------------ */
.contact {
	background: rgb(52, 58, 64);
	background: var(--primary-dark);
	padding: 3rem 0;
}

.contact h1 {
	color: #f9ab00;
	color: var(--primary-orange);
	padding: 1.3rem 0;
	text-transform: uppercase;
}

.contact p {
	color: #f4f4f4;
	color: var(--primary-light-grey);
	font-size: 1.2rem;
}

/* -------INPUTS------ */

.contact input,
.contact textarea {
	width: 100%;
	box-sizing: border-box;
	background: none;
	resize: none;
	border: 0;
	border-bottom: 0.125rem solid #666666;
	border-bottom: 0.125rem solid var(--primary-dark-grey);
	border-radius: 0;
	color: #f9ab00;
	color: var(--primary-orange);
	outline: 0 !important;
	box-shadow: none !important;
	margin-bottom: 1rem;
	padding-left: 0 !important;
}

.contact .container {
	padding: 4rem 0;
}

.contact input:focus,
.contact textarea:focus {
	background: transparent;
	color: #f9ab00;
	color: var(--primary-orange);
	border-bottom: 0.125rem solid #666666;
	border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contact textarea {
	height: 12.2rem !important;
}

.contact-btn {
	margin: 1rem 0 0 0;
	width: 100%;
	background: none;
}

.line {
	position: relative;
	top: -0.8rem;
	width: 0;
	height: 0.125rem;
	background: #f9ab00;
	background: var(--primary-orange);
	display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
	width: 100%;
	transition: width 0.5s ease-in-out;
}

input.form-control {
	margin: -0.3rem 0;
}

textarea.form-control {
	margin: -0.3rem 0;
}

/* ---------MEDIA------------- */
@media (max-width: 768px) {
	.contact {
		overflow-x: hidden;
	}

	.contact .container {
		padding: 0 1rem;
	}

	.contact p {
		padding: 0 0.4rem;
	}
}

